// Миксины для медиа-правил по брекпоинтам

@mixin before-mobile {
  @media (max-width: 479px) {
    @content;
  }
}
@mixin mobile {
  @media (min-width: 480px) {
    @content;
  }
}
@mixin before-tablet {
  @media (max-width: 767px) {
    @content;
  }
}
@mixin tablet {
  @media (min-width: 768px) {
    @content;
  }
}
@mixin before-desktop {
  @media (max-width: 1279px) {
    @content;
  }
}
@mixin desktop {
  @media (min-width: 1280px) {
    @content;
  }
}
